<template>
    <div class="personal-content-body">
        <div class="content">
            <!-- <div class="content-title clear">
                <div class="left">打卡记录</div>
            </div> -->
            <div class="flex_between">
            		<!-- <el-breadcrumb separator-class="el-icon-arrow-right navBar">
	                <el-breadcrumb-item :to="{name:'administrativeManageHomepageNew'}">行政管理</el-breadcrumb-item>
	                <el-breadcrumb-item>打卡记录</el-breadcrumb-item>
	            </el-breadcrumb> -->
	            <el-input placeholder="搜索" v-model="keyWord" class="header-right-time" :clearable='true' @change="toGetDataList(1)">
		          <i slot="prefix" class="el-input__icon el-icon-search "></i>
		        </el-input>
            </div>
            
            <!-- <div class="credit-box credit-box1">
                        <span>展示列：</span>
                        <div class="credit_right">
                            <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                                <el-checkbox v-for="(item,index) of cities" :key="index" :label="item.paramsKey">{{item.paramsValue}}</el-checkbox>
                            </el-checkbox-group>
                        </div>
                    </div> -->
            <div class="credit-box credit-box1 flex_between">
            		<div class="">
            			<span>时间：</span>
	                <el-date-picker v-model="BeginDate" :editable='false' type="date" placeholder="开始日期" value-format="yyyy-MM-dd" :clearable="false" @change='toGetDataList(1)' class="credit-box-data-select  credit-box-data-select1">
	                </el-date-picker>
	                <span>至</span>
	                <el-date-picker v-model="LastDate" :editable='false'  type="date" placeholder="结束日期" value-format="yyyy-MM-dd" :clearable="false" @change='toGetDataList(1)' class="credit-box-data-select credit-box-data-select2">
	                </el-date-picker>
            		</div>
                <div class="">
                		<el-checkbox v-model="isOrderByPeople" @change="toGetDataList(1)">人员排序</el-checkbox>
                </div>
            </div>
            <div class="line"></div>
            <div class="daochu_btn">
                <div class="left" @click="todownLoadExcel">
                    <div class="table-group-title-btn btn-blue">
                        导出报表
                    </div>
                </div>
            </div>
            <!-- <el-table :data="haveFilterData" border style="width: 100%" max-height="400" class="attendanceDetails_table">
                        <el-table-column v-for="(item,index) of headData" :index="index" :key="index"  :prop="item.titleKey" :label="item.titleValue" fit  min-width='150px' v-if="checkedCities.indexOf(item.type)!=-1||item.type==0" >
                        </el-table-column>
                    </el-table> -->
            <div class="historyapply-group-tablebox">
                <div class="table-normal-box">
                    <table class="table-normal">
                        <tr>
                            <th>序号</th>
                            <th>姓名</th>
                            <th>星期</th>
                            <th>时间</th>
                            <th>地点</th>
                            <th>审批事由</th>
                            <th>关联审批</th>
                        </tr>
                        <tr v-for="(item,index) in dataList" :key="index">
                            <td width='30'>{{pageSize*(currentPage-1)+index+1}}</td>
                            <td width='50'>{{item.user_name}}</td>
                            <td width='50'>{{getDay(item.datetime)}}</td>
                            <td >{{item.datetime}}</td>
                            <td>{{item.place}}</td>
                            <td>{{item.approval_info==null||item.approval_info==''?'--':item.approval_info}}</td>
                            <td>{{item.title==null||item.title==''?'--':item.title}}</td>
                        </tr>
                    </table>
                    <div class="table-nodata" v-if="total==0">
                        <img src="../../images/table_nodata.png"> 没有对应数据
                    </div>
                </div>
                <div class="pagination-normal" v-if="total!=0">
                    <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        cqdkList,
        cqdkExcel,
    } from "../../config/api";
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from "../../config/mUtils";
    export default {
        data() {
            return {
                total: 0,
                currentPage: 1,
                pageSize: 10,
                dataList: [],
                LastDate: '',
                BeginDate: '',
                keyWord: '',
                isOrderByPeople: false,
            };
        },
        mounted() {},
        created() {
            let LastDate = new Date().Format("yyyy-MM-dd");
            let BeginDate = new Date().Format("yyyy-MM-dd").split("-");
            BeginDate.splice(2, 1, "01");
            BeginDate = BeginDate.join("-");
            this.BeginDate = BeginDate;
            this.LastDate = LastDate;
            this.toGetDataList(1);
        },
        methods: {
            //打卡列表
            toGetDataList(val) {
            		if (this.isOrderByPeople) {
            			var is_order_people = 1;
            		} else{
            			var is_order_people = 0;
            		}
          		this.currentPage = val;
                let params = {
                    page: val,
                    pageSize: this.pageSize,
                    search: this.keyWord,
                    is_order_people: is_order_people
                };
                if (this.BeginDate == "" || this.LastDate == "") {
                    dialogConfirm("info", "请选择时间");
                    return false;
                } else {
                    let data1 = new Date(this.BeginDate);
                    let data2 = new Date(this.LastDate);
                    if (data1.getTime() > data2.getTime()) {
                        dialogConfirm("info", "开始时间不能大于结束时间");
                        return false;
                    } else {
                        params.BeginDate = this.BeginDate;
                        params.LastDate = this.LastDate;
                    }
                }
                cqdkList(params).then(res => {
                    if (res.data.msgNo == 1) {
                        this.dataList = res.data.payload.data;
                        this.total = res.data.payload.total;
                    } else {
                        dialogConfirm('info', res.data.message);
                    }
                });
            },
            //导出出勤报表
            todownLoadExcel() {
            		if (this.isOrderByPeople) {
            			var is_order_people = 1;
            		} else{
            			var is_order_people = 0;
            		}
                cqdkExcel({
                    BeginDate: this.BeginDate,
                    LastDate: this.LastDate,
                    search: this.keyWord,
                    is_order_people: is_order_people
                }).then(res => {
                    if (res.data.msgNo == 1) {
                        window.location.href = res.data.payload;
                    } else {
                        dialogConfirm('info', res.data.message)
                    }
                });
            },
            currentChange(val) {
                if (this.total > this.pageSize) {
                    this.currentPage = val;
                    this.toGetDataList(val)
                }
            },
            getDay(data) {
                let d = new Date(data);
                let data1 ='';
                if(d.getDay()==0){
                    data1 = '星期天'
                }else if(d.getDay()==1){
                     data1 = '星期一'
                }else if(d.getDay()==2){
                     data1 = '星期二'
                }else if(d.getDay()==3){
                     data1 = '星期三'
                }else if(d.getDay()==4){
                     data1 = '星期四'
                }else if(d.getDay()==5){
                     data1 = '星期五'
                }else if(d.getDay()==6){
                     data1 = '星期六'
                }
                return data1
            }
        }
    };
</script>

<style lang="scss" scoped>
    .content {
        width: 100%;
        min-height: 100%;
        // min-width: 1000px;
        background-color: white;
        padding: 20px 30px;
    }
    
    .content-title {
        border-bottom: 1px solid #f1f1f1;
        margin-bottom: 15px;
    }
    
    .credit-box {
        display: flex;
        align-items: baseline;
        margin-bottom: 10px;
        &>span {
            font-size: 14px;
            color: #35383a;
        }
        &>span:nth-child(1) {
            margin-right: 20px;
        }
        .credit_right {
            width: calc(100% - 200px);
        }
    }
    
    .credit-box1 {
        margin-top: 20px;
    }
    
    .credit-box .el-checkbox {
        margin-right: 30px;
        margin-left: 0px;
        /*margin-bottom: 5px;*/
    }
    
    .credit-box .el-checkbox__label {
        color: #35383a !important;
    }
    
    .credit-box-data-select1 {
        margin-right: 8px;
    }
    
    .credit-box-data-select2 {
        margin-left: 8px;
        margin-right: 100px;
    }
    
    .select-department {
        margin-right: 18px;
    }
    
    .tishi {
        color: #a0a4aa;
    }
    
    .line {
        widows: 100%;
        border-bottom: 1px solid #f1f1f1;
        margin: 20px 0px;
    }
    
    .daochu_btn {
        width: 100%;
        height: 30px;
        margin-bottom: 20px;
    }
    
    // .gutter{
    //     border-bottom: none !important; 
    //     border-bottom-width: 0px !important;
    // }
</style>



